import React from "react";
import '../Api.css';
import Footer from "../../components/footer";
import APITopbar from "../components/api-topbar";
import Sidebar from "../components/sidebar";
import "../css/api-sidebar.css";
import Type from "../components/api-type";
import Prop from "../components/api-prop";
import PropSingle from "../components/api-prop-singular";
import Func from "../components/api-function";
import SidebarInfo from "../sidebar-info";
import ClassMeta from "../components/class-meta";

const Bitmap = () => {
    return (
        <div className="API">
            <title>API | OSGL</title>
            <APITopbar />

            <div className="sidebar-content">
                <Sidebar links={SidebarInfo} />
            </div>

            <div className="api-content">
                <h1 className="api-header">Enum</h1>
                <div className="api-header-line"></div>

                <ClassMeta metadata={{
                    core: true,
                    abstract: true,
                    singleton: true,
                    interface: true,
                    threadSafe: true,
                }} />

                <p className="api-paragraph">Enums are used for error-handling and type-checking in OSGL. This page will guide you on what each enum does, and means.</p>

                <h1 className="api-header">Properties</h1>
                <div className="api-header-line"></div>

                <h3 id="windowerrors" className="api-header">Window Errors</h3>
                <PropSingle fullName="Enum.WindowError" type="WindowError" desc="A collection of errors relating to that of a Window." />

                <PropSingle fullName="Enum.WindowError.InvalidCreationInstance" type="number" desc="(Error 0) Attempt to create a Window on an object that cannot be drawn to." />
                <PropSingle fullName="Enum.WindowError.WindowDimensionsOutOfBounds" type="number" desc="(Error 1) Attempt to create a Window with its size being less than 0, or higher than 1024. This error can also occur if the width or height provided is nil." />
                <PropSingle fullName="Enum.WindowError.APINotEnabled" type="number" desc="(Error 8) The EditableImage / EditableMesh API is not enabled. You can enable it from your games settings." />

                <h3 id="drawableerrors" className="api-header">Drawable Object Errors</h3>
                <PropSingle fullName="Enum.DrawableObjectError" type="DrawableError" desc="A collection of errors relating to DrawableObjects." />

                <PropSingle fullName="Enum.DrawableObjectError.NotEnoughMemory" type="number" desc="(Error 2) Device does not have enough memory to perform this action." />
                <PropSingle fullName="Enum.DrawableObjectError.ResizeOutOfBounds" type="number" desc="(Error 3) Attempt to resize an Object to a size smaller than 0, or bigger than 1024." />
                <PropSingle fullName="Enum.DrawableObjectError.BadBufferSize" type="number" desc="(Error 4) Attempt to deserialize a buffer, where the dimensions provided are invalid." />
                <PropSingle fullName="Enum.DrawableObjectError.OutOfBounds" type="number" desc="(Error 5) Attempt to perform an operation out of bounds." />

                <h3 id="textureerrors" className="api-header">Texture Errors</h3>
                <PropSingle fullName="Enum.TextureError" type="TextureError" desc="A collection of errors relating to Textures." />
                
                <PropSingle fullName="Enum.TextureError.BadInput" type="number" desc="(Error 6) Attempt to load a corrupted, invalid, or outdated texture." />

                <h3 id="fonterrors" className="api-header">Font Errors</h3>
                <PropSingle fullName="Enum.FontError" type="FontError" desc="A collection of errors relating to Fonts and rendering text." />
                
                <PropSingle fullName="Enum.FontError.BadInput" type="number" desc="(Error 7) Attempt to load a corrupted, invalid, or outdated font." />

                <div style={{ height: "55px" }} />
            </div>
            <Footer />
            <div className="grid-background-dark"></div>
        </div>
    );
};

export default Bitmap;
